@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 20px 40px;
  font-size: 1.2rem;
  letter-spacing: 1px;
  background: #fafafa;
}
h1,
h2,
h3,
h4,
ul,
li,
a,
p,
input,
label,
button,
div {
  margin: 0;
  padding: 0;
  font-family: "Quicksand", sans-serif;
  color: #444;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 120px;
}
nav ul {
  display: flex;
  align-items: center;
}
nav li {
  margin-left: 20px;
}
nav li a {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 0.8em;
  display: block;
  padding: 10px 16px;
  letter-spacing: 2px;
}
.btn {
  border-radius: 36px;
  background: #fee996;
}
form h2 {
  font-size: 2.4em;
  font-weight: 900;
  margin-bottom: 40px;
}
form {
  width: 360px;
  margin: 0 auto;
  padding: 30px;
  box-shadow: 1px 2px 3px 4px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  background: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
input {
  padding: 10px 12px;
  border-radius: 4px;
  border: 1px solid #ddd;
  font-size: 1em;
  width: 100%;
}
label {
  display: block;
  margin: 20px 0 10px;
}
button {
  border-radius: 36px;
  background: #fee996;
  border: 0;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 0.8em;
  display: block;
  padding: 10px 16px;
  letter-spacing: 2px;
}
.error {
  color: #ff0099;
  margin: 10px 2px;
  font-size: 0.8em;
  font-weight: bold;
}
header {
  display: flex;
  align-items: center;
}
header img {
  width: 250px;
  margin-right: 40px;
}
header h2 {
  font-size: 3em;
  margin-bottom: 10px;
}
header h3 {
  font-size: 1.6em;
  margin-bottom: 10px;
  margin-left: 2px;
  color: #999;
}
header .btn {
  margin-top: 20px;
  padding: 12px 18px;
  text-transform: uppercase;
  font-weight: bold;
  display: inline-block;
  font-size: 0.8em;
}

.submit__btn {
  margin-top: 5%;
}

.Link {
  color: rgb(42, 80, 202) !important;
  text-decoration: none;
}
.Link:hover {
  color: rgb(124, 123, 123) !important;
  text-decoration: none;
}

footer {
  text-align: center;
  height: 40px;
  color: #aaa;
}
@media only screen and (max-width: 767px) {
  form {
    width: 75vw;
  }
  .container_table {
    border: none !important;
  }
}

/* google fonts */

.App {
  text-align: center;
}
.allButFooter {
  min-height: calc(100vh - 95px);
}

/* NavBar css */
.continer__NavBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: lightgray;
}
.Link_Nav {
  text-decoration: none;
  color: #444;
  font-weight: bold;
  font-size: 28px;
  margin-left: 30px;
  margin-right: 30px;
}
.Link_Nav:hover {
  color: rgb(0, 0, 0);
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}
.authDiv {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.searchDiv {
  flex-direction: row;
  position: absolute;
  right: 0;
  top: 0;
  margin-right: 23.2%;
  bottom: 0;
}
.main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.container_table {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 48rem;
  border-radius: 0.6rem;

  margin-top: 3rem;
  position: relative;
  max-width: 95%;
}
table {
  margin-bottom: 0 !important;
}

td {
  text-align: center;
  vertical-align: middle;
}
a {
  text-decoration: none !important;
  color: #444;
  font-size: 1rem;
}
a:hover {
  color: #444;
}

.showMore:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  color: #444 !important;
}
.addTransaction {
  background-color: #fee996 !important;
}
.DeletePortfolio {
  background-color: #fe9696 !important;
}
.middleInfoDiv {
  margin-top: 1.5%;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  background: rgba(138, 138, 138, 0.102);
}

.hideMoreInfo {
  display: none;
}

.IconForInfo:hover + .hideMoreInfo {
  display: block;
  color: #444;
}
.portfolios {
  padding: 5%;
  display: grid;
  grid-row: auto;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 1% !important;
}
.Portfolio__card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  cursor: pointer;
}
.Portfolio__card {
  z-index: -1 !important;
}

.search__css {
  position: absolute;
  z-index: 1000 !important;
  width: auto;
  left: -40vw;
}
.admin__div {
  width: 70vw !important;
  display: flex;
  flex-direction: row-reverse;
}
.Link_Nav_profile {
  text-decoration: none;
  color: #444;
  font-weight: bold;
  font-size: 28px;
  margin-right: 4%;
}

.Link_Nav_profile:hover {
  cursor: pointer;
}
@media only screen and (min-width: 1800px) {
  .search__css {
    position: absolute;
    z-index: 1000 !important;
    width: auto;
    left: -35vw;
  }
}

@media only screen and (min-width: 1024px) {
  .authDiv {
    flex-direction: row;
    position: absolute;
    right: 0;
    top: 0;
    margin-right: 5%;
    margin-top: 0.5%;
    margin-bottom: 0.5%;
    bottom: 0;
  }

  .admin__div {
    width: 70vw !important;
    display: flex;
    flex-direction: row-reverse;
  }
}

@media only screen and (max-width: 1024px) {
  .portfolios {
    padding: 5%;
    display: grid;
    grid-row: auto;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .admin__div {
    width: auto !important;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
}
@media only screen and (min-width: 769px) and (max-width: 900px) {
  .portfolios {
    padding: 5%;
    display: grid;
    grid-row: auto;
    grid-template-columns: 1fr 1fr;
  }
  .admin__div {
    width: auto !important;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
}
@media only screen and (max-width: 768px) {
  .authDiv {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .portfolios {
    padding: 5%;
    display: grid;
    grid-row: auto;
    grid-template-columns: 1fr;
  }
  .admin__div {
    width: auto !important;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .onPhone {
    display: none;
  }
  .columnBtns {
    flex-direction: column !important;
  }
  .coinNamediv {
    display: block !important;
  }
}
@media only screen and (min-width: 1800px) {
  .search__css {
    position: absolute;
    z-index: 1000 !important;
    width: auto;
    left: -35vw;
  }
}

.searchform {
  width: auto;
  height: auto;
  padding: 0;
  z-index: 1000 !important;
}
#input[type="text"] {
  width: 100%;
  border: none;
  border-bottom: 1px solid #645979;
  outline: none;
  border-radius: 5px 5px 0 0;
  background-color: #ffffff;
  font-size: 16px;
}
ul {
  list-style: none;
  display: grid !important;
  grid-template-columns: 1fr;
}
.list {
  width: 100%;
  background-color: #ffffff;
  border-radius: 0 0 5px 5px;
}
.list-items {
  padding: 10px 5px;
}
.list-items:hover {
  background-color: #dddddd;
}

.addTrans {
  width: 360px;
  margin: 0 auto;
  padding: 30px;
  box-shadow: 1px 2px 3px 4px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  background: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .addTrans {
    width: 75vw;
  }
}

.account-settings .user-profile {
  margin: 0 0 1rem 0;
  padding-bottom: 1rem;
  text-align: center;
  color: #fee996;
}
.account-settings .user-profile .user-avatar {
  margin: 0 0 1rem 0;
}

.account-settings .user-profile h5.user-name {
  margin: 0 0 0.5rem 0;
}
.account-settings .user-profile h6.user-email {
  margin: 0;
  font-size: 0.8rem;
  font-weight: 400;
  color: #9fa8b9;
}

.form-control {
  border: 1px solid #cfd1d8;
  border-radius: 2px;
  font-size: 0.825rem;
  background: #ffffff;
  color: #2e323c;
}
.headercolor {
  color: #444;
  font-weight: bold;
}

